@import url(https://fonts.googleapis.com/css2?family=Abhaya+Libre:wght@800&family=Josefin+Sans:wght@100;300;400&&display=swap);
.home .time {
  font-weight: 100;
}

.callout-content {
  width: 100%;
  max-width: 620px;
  margin: 1rem auto;
}

.section-title {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
}

.time-and-details {
  text-align: center;
  margin-bottom: 2rem;
}

.time-and-details:last-child {
  margin-bottom: 0;
}

.section__content-container {
  width: 100%;
}

.date {
  text-align: center;
}

.home .icon-container {
  font-size: 3rem;
}

.subsection-title {
  margin-bottom: 0;
  margin-top: 0;
}

.home .section {
  display: block;
  margin-bottom: 1rem;
}

.section__title-container {
  width: 100%;
}

.home .attire {
  font-weight: 100;
  font-size: 0.9rem;
  font-style: italic;
}

@media all and (min-width: 768px) {
  .home .section {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }

  .section__title-container {
    padding: 2rem 0;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .section__content-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.footer {
  width: 100%;
  padding: 1rem 0;
}

.footer__content {
  width: 100%;
  max-width: 200px;
  margin: 0 auto;
  text-align: center;
}

.initials {
  font-size: 5rem;
  font-family: Abhaya Libre ExtraBold;
}

.footer__date {
  font-weight: 100;
}

.page__body {
  width: 100%;
  padding-top: 8rem;
  display: inline-block;
}

.background-image {
  width: 100%;
  display: block;
  max-width: 2000px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Abhaya Libre;
  font-weight: bold;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

@media all and (min-width: 768px) {
  .page__body {
    padding-top: 0;
  }
}

.nav {
  width: 100%;
}

.nav__items {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
}

.nav__item {
  padding: 0.25rem 0.5rem;
}

.nav__link {
  display: block;
  text-decoration: none;
  padding: 0.5rem;
  margin: 0;
  font-size: 1rem;
  font-weight: 300;
  color: #111;
  border-bottom: 2px solid rgb(1, 1, 1, 0);
}

.nav__link:hover,
.nav__link.active {
  color: #999;
  border-bottom: 2px solid #999;
}

@media all and (min-width: 768px) {
  .nav__items {
    justify-content: center;
  }

  .nav__link {
    font-size: 1.25rem;
  }
}

.page__header {
  position: fixed;
  width: 100%;
  top: 0;
  background-color: rgba(255, 255, 255, 0.9);
  transition: all 0.25s;
}

.site-name {
  text-align: center;
  line-height: 1;
  font-family: Abhaya Libre ExtraBold;
  color: #000;
  font-size: 2rem;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  margin: 1rem;
  transition: all 0.5s;
}

.date-info {
  height: 60px;
  text-align: center;
  font-size: 0.75rem;
  font-family: Josefin Sans;
  letter-spacing: 0.25rem;
  color: #000;
  font-weight: 300;
  text-transform: uppercase;
  transition: all 0.25s;
  overflow: hidden;
}

.countdown {
  font-size: 0.9rem;
  font-weight: 100;
  padding: 0.5rem;
}

@media all and (min-width: 768px) {
  .site-name {
    font-size: 4rem;
  }
  .date-info {
    font-size: 1.5rem;
    height: 80px;
  }
}

.page__header--scrolled .site-name {
  font-size: 0.5rem;
}

.page__header--scrolled .date-info {
  height: 0;
  font-size: 1px;
}

.shrink-wrap {
  overflow: hidden;
}

.page__header--scrolled .countdown {
  font-size: 0.0825rem;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Josefin Sans, Arial, Helvetica, sans-serif;
}

p,
a {
  font-family: Josefin Sans, Arial, Helvetica, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.text-center {
  text-align: center;
}

.flex {
  display: flex;
}

